@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Manrope', sans-serif;
}

body {
  background: #202632;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;

}

.card {
  background: #313a49;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 540px;
  padding: 3rem 2rem 4.5rem 2rem;
  margin: 0 auto;
  border-radius: 15px;
  position: relative;
}

p {
  color: #4ba07f;
  letter-spacing: 3.5px;
  font-weight: 700;
  margin-bottom: 1rem;
  font-size: 12px;

}

h2 {
  color: #c5d6e0;
  font-size: 28px;
  margin-bottom: 2.5rem;

}

.dice {
  background: #54ffaa;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: absolute;
  bottom: -2rem;


}

.dice:hover {
  box-shadow: 0 0 40px 0 rgba(82, 255, 169, 1);
  transition: .2s ease-in;

}

.divider-mobile {
  display: none;
}

@media (max-width:924px) {
  .divider-mobile {
    display: block;
  }

  .divider-desktop {
    display: none;
  }
}

@media (max-width:620px) {
  .card {
    width: 90%;

  }
}

@media(max-width:375px) {
  h2 {
    font-size: 24px;
  }

  p {
    font-size: 11px;
  }
}